import { FETCH_USER, FETCH_SURVEYS } from './../types/types';

export const fetchUser = () => async dispatch => {
  // // const res = await axios.get('/api/current_user');

  const res = await new Promise(resolve => {
    setTimeout(() => {
      resolve('heyy');
    }, 2000)
  })
  dispatch({ type: FETCH_USER, payload: res });
};



